* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #f0f4f2;
}
body nav {
  position: fixed;
  height: 4rem;
  background-color: rgba(240, 244, 242, 0.2);
  width: 100%;
  z-index: 5;
  padding-top: 0.2rem;
}
body nav ul {
  display: flex;
  justify-content: end;
  align-items: center;
}
body nav ul li {
  display: inline-block;
  font-family: "Josefin Sans", sans-serif;
  font-size: 1.5rem;
  font-weight: 100;
  color: #815349;
  padding: 1rem;
  margin: 0 0.5rem;
}
body nav ul li:hover {
  text-shadow: 5px -1px 3px rgba(131, 84, 73, 0.5);
}
body h1 {
  color: orangered;
}
body .hero-sect {
  width: 100vw;
  display: flex;
}
body .hero-sect .hero-img {
  background-image: url("../../../assets/zac-sturgeon-sDzof1Ps89s-unsplash.jpg");
  width: 70%;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  border-bottom-right-radius: 15px;
  border-top-right-radius: 15px;
}
body .hero-sect .blur-bar {
  height: 108vh;
  width: 8rem;
  background-color: #f0f4f2;
  position: relative;
  left: -3rem;
  filter: blur(20px);
}
body .hero-sect .blur-base {
  width: 20%;
  height: 20rem;
  border: 2px solid #815349;
}
body .hero-sect .hero-text {
  font-family: "Josefin Sans", sans-serif;
  justify-content: end;
  width: 48rem;
  align-self: flex-end;
  margin-right: 1.5rem;
  margin-bottom: 5rem;
  text-align: right;
  line-height: 2.5rem;
  letter-spacing: 0.3rem;
}
body .hero-sect .hero-text p:first-of-type {
  color: #815349;
  font-size: 2.5rem;
  text-transform: uppercase;
}
body .hero-sect .hero-text p:nth-of-type(2),
body .hero-sect .hero-text p:nth-of-type(3) {
  color: #815349;
  font-size: 1.5rem;
}
body .hero-sect .hero-text .hero-social img {
  width: 1.5rem;
  height: 1.5rem;
  margin-right: 0.3rem;
  margin-top: 0.5rem;
}
body .mission-sect {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
body .mission-sect p {
  width: 60%;
  font-family: "Josefin Sans", sans-serif;
  color: #815349;
  font-size: 1rem;
}
body .work-sect {
  height: 100vh;
  font-family: "Josefin Sans", sans-serif;
  font-size: 1.5rem;
  display: flex;
  flex-direction: column;
}
body .work-sect li {
  border: 1.5px solid #815349;
  width: fit-content;
  padding: 1rem;
  color: #815349;
}
